import React, { useState } from 'react';

// <DataRow data={d} allData={ dataRows } updateData={ setDataRows } />

export default function DataRow(props) {

  //console.log("Row: ")
  //console.log(props.allData);

  function removeEmailHere(email) {
    const updatedData = props.allData.map( d => {
      if (d.key === props.data.key) {
        const currEmailList = d['Mail List'].split(',').filter( d => d !== email );
        return {...d, 'Mail List': currEmailList.join(',')};
      } else {
        return d;
      }
    });
    props.updateData(updatedData);
  }

  function removeFaxHere(fax) {
    const updatedData = props.allData.map( d => {
      if (d.key === props.data.key) {
        const currFaxList = d['Fax List'].split(',').filter( d => d !== fax );
        return {...d, 'Fax List': currFaxList.join(',')};
      } else {
        return d;
      }
    });
    props.updateData(updatedData);
  }

  function removeEmailAll(email) {
    let remCounter = 0; //count removed email addresses
    const updatedData = props.allData.map( d => {
      if (d.isChecked === true) {
        const currEmailList = d['Mail List'].split(',').filter( d => {
          let logicalTest = d.toUpperCase() !== email.toUpperCase();
          remCounter=logicalTest?remCounter:remCounter+1;
          return logicalTest; 
        });
        return {...d, 'Mail List': currEmailList.join(',')};
      } else {
        return d;
      }
    });
    props.updateData(updatedData);
    alert(`Removed ${remCounter} email address${remCounter!==1?'es':''}`)
  }

  function removeFaxAll(fax) {
    let remCounter = 0; //count removed fax numbers
    const updatedData = props.allData.map( d => {
      if (d.isChecked === true) {
        const currFaxList = d['Fax List'].split(',').filter( d => {
          let logicalTest = d !== fax;
          remCounter=logicalTest?remCounter:remCounter+1;
          return logicalTest;
        });
        return {...d, 'Fax List': currFaxList.join(',')};
      } else {
        return d;
      }
    });
    props.updateData(updatedData);
    alert(`Removed ${remCounter} fax number${remCounter!==1?'s':''}`)
  }


  const emailList = props.data['Mail List'] && props.data['Mail List'].split(',').map( email => {
    if (email) {
      return (
        <li className="list-group-item">
          <div className="container">
            <div clasName="row">
              <div className="col align-self-start">
                {email}
              </div>
              <div className="col align-self-end">
                <button type="button" className="btn btn-sm btn-outline-danger" onClick={ () => removeEmailHere(email) }>Remove Here</button>
                <button type="button" className="btn btn-sm btn-danger" onClick={ () => removeEmailAll(email) }>Remove in selected</button>
              </div>
            </div>
          </div>
        </li>
      )}});

  const faxList = props.data['Fax List'] && props.data['Fax List'].split(',').map( fax => {
    if (fax) {
      return (
        <li className="list-group-item">
          <div className="container">
            <div clasName="row">
              <div className="col align-self-start">
                {fax}
              </div>
              <div className="col align-self-end">
                <button type="button" className="btn btn-sm btn-outline-danger" onClick={ () => removeFaxHere(fax) }>Remove Here</button>
                <button type="button" className="btn btn-sm btn-danger" onClick={ () => removeFaxAll(fax) }>Remove in selected</button>
              </div>
            </div>
          </div>
        </li>
      )}});

  function toggleCheckbox() {
    let toggledData = props.allData.map( d => {
      if (d.key === props.data.key ) {
        return { ...d, isChecked: !d.isChecked };
      }
      return d;
    });
    props.updateData(toggledData);
    props.data.isChecked = !props.data.isChecked;
  }

  return(
    <tr key={props.data.key}>
          <td>
      <input 
        className="form-check form-check-input" 
        type="checkbox" 
        checked={ props.data.isChecked }
        onChange={ toggleCheckbox }
      />
    </td>
    <td>{ props.data['Regn'] }</td>
    <td>{ props.data['Level'] }</td>
    <td>{ props.data['ID'] }</td>
    <td>{ props.data['Linked report'] }</td>
    <td><ul className="list-group"> { emailList } </ul></td>
    <td><ul className="list-group"> { faxList } </ul></td>
    </tr>
  );
}