import React, { useState } from 'react';
import { addKeyCheck } from './Table';

const STUB = {
  //region: '', code: 'CLT', level: '', accnumber: '', email: '', fax: ''
  Regn: '', Level: 'CLI', ID: '', 'Linked report': '', 'Mail List': '', 'Fax List': ''
};

const REGIONLEVELS = {
  DBOF: ["", "PERIOSTD-DBOF"],
  DBAO: ["", "DBAO-Contract-Note", "DBAO-Contract-Note-Transfer"],
  DBAR: ["", "DBAR-Contract-Note", "DBAR-Contract-Note-Transfer"],
  DBJH: ["", "dbjh-confirm-1", "dbjh-confirm-2", "dbjh-confirm-AD", "dbjh-confirm-t", 
         "DBJH-contract-note", "DBJH-Contract-Note-Transfer", "djallcch", "doallcch", 
         "ICTA-MRG-LU238", "ICTA-MRG-LU348", "ICTA-MRG-LU349", "PERIOSTD-DBJH"],
  DBMM: ["", "DBMM-Contract-Note", "DBMM-Contract-Note-Transfer"]
}

const REGIONS = Object.keys(REGIONLEVELS);

let firstLoad = true;
/*
<TableHeader 
  globalCheck={globalCheck} toggleSelected={ toggleSelected }
  allData={ dataRows } updateData={ setDataRows }
/>
*/

export default function TableHeader(props) {
  const [ accNumber, setAccNumber ] = useState('');
  const [ emailAddress, setEmaiAddress ] = useState('');
  const [ faxNumber, setFaxNumber ] = useState('');
  const [ accNumberAlert, setAccNumberAlert ] = useState("alert-danger");

  //select in the header
  function makeRegionSelect() {
    const selOpt = REGIONS.map( v => (
      <option value={v}>{v}</option>
    ));
    return(
      <select className="form-select form-select-sm" onChange={ regionChanged }>
        {selOpt}
      </select>
    )
  }

  // start with the first region
  if (firstLoad) {
    firstLoad = false;
    const newRegion = REGIONS[0];
    let newData = REGIONLEVELS[newRegion].map( lvl => {
      return {...STUB, 'Regn': newRegion, 'Linked report': lvl}
    });
    props.updateData(addKeyCheck(newData));
  }

  function regionChanged(e) {
    const newRegion = e.target.value;
    let newData = REGIONLEVELS[newRegion].map( lvl => {
      return {...STUB, 'Regn': newRegion, 'Linked report': lvl}
    });
    //console.log(newRegion);
    props.updateData(addKeyCheck(newData));
  }

  function updateAccNo(e) {
    setAccNumber(e.target.value);
    validateAccNo(e.target.value);
  }

  function changeAccNo(e) {
    e.preventDefault();
    if (validateAccNo(accNumber) && props.allData) {
      const updatedData = props.allData.map( d => {
        return d.isChecked?{...d, 'ID': accNumber.toUpperCase()}:d;
      });
      props.updateData(updatedData);
    } else {
      alert("Invalid account number");
    };
  }

  function validateAccNo(anum) {
    let regMatch = /[a-z0-9]{3}\d{7}/gmi.exec(anum);
    if ((anum && anum.length==10) && (regMatch && regMatch.length > 0)) {
      setAccNumberAlert("alert-success");
      return true;
    }
    setAccNumberAlert("alert-danger");
    return false; 
  }

  function validateEmailAddress(emadd) {
    //const regMatch = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/g;
    const regMatch = /[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/gmi
    let regEx = regMatch.exec(emadd);
    if(regEx && regEx.length > 0) {
      return true;
    } 
      return false;
  }

  function updateCurrentEmail(e) {
    setEmaiAddress(e.target.value.trim());
  }
  function addEmail(e) {
    e.preventDefault();
    const updatedData = props.allData.map( d => {
      if (d.isChecked && validateEmailAddress(emailAddress)) {
        let previousEmailList = d['Mail List'].split(",");
        if (!previousEmailList.find(e => e.toUpperCase() === emailAddress.toUpperCase() )) {
          let updatedEmailList = [d['Mail List'].split(","), emailAddress].join(',');
          //console.log(updatedEmailList);
          return {...d, 'Mail List': updatedEmailList };
        }
      }
      return d;
    });
    //console.log(updatedData);
    props.updateData(updatedData);
  }

  function updateCurrentFax(e) {
    setFaxNumber(e.target.value.trim());
  }

  function addFax(e) {
    e.preventDefault();
    const updatedData = props.allData.map( d => {
      if (d.isChecked) {
        let previousFaxList = d['Fax List'].split(",");
        if (!previousFaxList.find(e => e === faxNumber )) {
          let updatedFaxList = [d['Fax List'].split(","), faxNumber].join(',');
          //console.log(updatedEmailList);
          return {...d, 'Fax List': updatedFaxList };
        }
      }
      return d;
    });
    //console.log(updatedData);
    props.updateData(updatedData);
  }

  const regionSelect = makeRegionSelect();

  const tableHeader = (
    <tr>
      <th scope="col">
        <input 
          className="form-check form-check-input" 
          type="checkbox" checked={!props.globalCheck} 
          onClick={props.toggleSelected}
        />
      </th>
      <th scope="col">{ regionSelect }</th>
      <th scope="col">Level</th>
      <th scope="col">
        <form onSubmit={ changeAccNo }>
          <input type="search" onChange={ updateAccNo } />
          <button type="submit" className={ accNumberAlert }>change</button>
        </form>
      </th>
      <th scope="col">Linked report</th>
      <th scope="col">
        <form onSubmit={ addEmail }>
          <input type="search" onChange={ updateCurrentEmail }/>
          <button type="submit">add</button>
        </form>
      </th>
      <th scope="col">
        <form onSubmit={ addFax }>
          <input type="text" onChange={ updateCurrentFax }/>
          <button type="submit">add</button>
        </form>
      </th>
    </tr>
  );


  return(
    <thead>
      { tableHeader }
    </thead>
  );
}