import { useState } from 'react';
import Table, { addKeyCheck } from './components/Table';
import { dsvFormat } from 'd3';

const FILTER_MAP = {
  All: () => true,
  Active: task => !task.completed,
  Completed: task => task.completed
};

var buffer = [];

function App(props) {
  const [data, setData] = useState(props.data?addKeyCheck(props.data):null);
  const [csvData, setCsvData] = useState('');
  const [filter, setFilter] = useState("All");

  function csvParse(text) {
    const csvParser = dsvFormat(";");
    text = text.endsWith('\n')?text.slice(0, text.length-1):text;
    return csvParser.parse(text);
  }

  function handleFileChange(e) {
    readTxtFile(e.target.files[0]).then( d => console.log(d));
    //console.log();
  }

  async function readTxtFile(file) {
    const reader = new FileReader();
    reader.onload =  pe => {
      setData(addKeyCheck(csvParse(pe.target.result)));
    }
    reader.readAsText(file);
  }

  function generateCsv() {
    //console.log("genearting csv");
    //const headerNames = 'region,code,level,account,email,fax';
    let csvContent = data.filter(d => d.isChecked).filter(d => d['Mail List'].length>0 || d['Fax List'].length>0).map( d => {
      let emails = d['Mail List'].split(',').filter(e => e && e.length>0).join(',');
      let faxes = d['Fax List'].split(',').filter(f => f && f.length>0).join('#');
      //console.log(d.fax);
      return [d['Regn'], d['Level'], d['ID'], d['Linked report'], `${emails}`, `${faxes}`].join(';');
    });

    //csvContent = [headerNames, ...csvContent].join('\n');

    buffer.push(csvContent.join('\r\n'))

    setCsvData(buffer.join('\r\n')+'\r\n');
  }

  function clearCsv() {
    buffer = [];
    setCsvData("");
  }

  function copyToClipboard() {
    var copyText = document.getElementById("csvTextArea");
    copyText.select();
    document.execCommand("copy");
    alert(`Text copied to the clipboard: ${csvData.length} characters.`);
  }

  return (
    <div className="tab-content">
      <div className="tab-pane fade show active" id="main" role="tabpanel">
        <div className="table-responsive">
          <input type="file" onChange={handleFileChange} />
          <Table data={ data } updateData={ setData } filterFun={ FILTER_MAP[filter] }/>
        </div>
        <hr /><b>Pawel Piela</b> pawel.piela@rbc.com
      </div>
      <div className="tab-pane fade show" id="csv" role="tabpanel">
        <div className="container">
          <div className="row mt-3">
            <div className="col-auto me-auto">
              <button className="btn btn-sm btn-primary" onClick={ generateCsv }>Generate content of the load file</button>
              <button className="btn btn-sm btn-secondary mx-2" onClick={ copyToClipboard }>Copy to clipboard</button>
            </div>
            <div className="col-auto">
              <button className="btn btn-sm btn-danger" onClick={ clearCsv }>Clear</button>
            </div>
          </div>
        </div>
        <div className="container">
          <div id="csvText">
            <textarea id="csvTextArea" value={ csvData } />
          </div>
        </div>
      </div>
    </div>

    
  );
}

export default App;
