import React, { useState } from 'react';
import DataRow from './DataRow';
import TableHeader from './TableHeader';
import { nanoid } from 'nanoid';


export default function Table(props) {
  const [globalCheck, setGlobalCheck] = useState(false);

  const dataRows = props.data;
  
  function setDataRows(data) {
    props.updateData(data);
  }

  //const [dataRows, setDataRows] = useState(props.data?props.data:addKeyCheck(SAMPLEDATA));

  const dataRowsJSX = dataRows && dataRows.map( d => (
    <DataRow data={d} allData={ dataRows } updateData={ setDataRows } />
  ));

  //let headerList = Object.keys(dataRows[0]).filter(cname => cname!=="key"); //["#", "First Name", "Second Name"];
  //headerList = ['checkboxes', ...headerList];

  function toggleSelected() {
    setGlobalCheck(!globalCheck);
    setDataRows(dataRows.map( d => { return Object.assign(d, {isChecked: globalCheck})}));
    //console.log(dataRows[0]);
  }

  return (
    <table className="table table-sm table-success table-hover table-striped">
      <TableHeader 
        globalCheck={globalCheck} toggleSelected={ toggleSelected }
        allData={ dataRows } updateData={ setDataRows }
      />
      <tbody>{ dataRowsJSX }</tbody>
    </table>
  );
}

export function addKeyCheck(data) {
  return data.map(d => Object.assign({key: nanoid(), isChecked: true}, d));
}